<template>
  <div>
      <p> {{msg}} </p>
      <button type="" @click="changerUrl" > change moi !</button>
  </div>
</template>

<script>
export default {
    data() {
        return {
            msg: window.location
        }
    },
    methods: {
        changerUrl() {
            window.location.href = "http://www.google.com";
        }
    },
    mounted() {
        console.log("we are mounted");
        window.location.href = "http://www.google.com";
    },
}
</script>

